import * as React from 'react';
import { useSearchParams } from 'react-router-dom';

import './forms.scss';

import { getCN } from '@miq/utiljs';
import { Button } from '@miq/componentjs';
import Form, { Inputs, useForm } from '@miq/formjs';
import { CustomerFields, TCart, TCustomer, TOrderItem } from '@shopy/orderjs';
import { SizeQuantityField, SizeSelectField } from '@shopy/storejs';

import { patchOrderItem, postCustomer, postOrderItem } from './services';
import { ProductType } from './types';

export const CartForm = ({ product, cart, ...props }: { product: ProductType; cart?: TCart }) => {
  const { sizes } = product || {};

  console.log(sizes);

  const form = useForm<TCart>({});
  return <Form context={form}></Form>;
};

export const OrderItemForm = ({
  product,
  cart,
  fields,
  onSuccess,
  children,
  ...props
}: {
  product: ProductType;
  onSuccess: (p: TCart) => void;
  fields: string[];
  cart?: TCart;
  children?: React.ReactNode;
}) => {
  let { sizes = [], meta_slug: productSlug } = product || {};
  const { products = [], items = [] } = cart || {};

  const item = items.find((i: TOrderItem) => i?.product?.meta_slug === productSlug);
  // console.log(cart, items, item);

  console.log(item);

  const form = useForm<TOrderItem>({
    quantity: 1,
    size: item ? item.size : '',

    //# customer
    first_name: '',
    last_name: '',
    email: '',
    phone: '',
  });

  const { errors, values } = form;

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    if (!values.size) {
      form.setError('size', 'Veuillez choisir votre taille');
      return;
    }

    const fd: any = { quantity: values.quantity, size: values.size };

    if (item && item.slug) {
      patchOrderItem(item.slug!, fd).then(({ data }) => {
        onSuccess?.(data);
      });
    } else {
      fd['customer'] = form.values;
      postOrderItem(productSlug!, fd).then(({ data }) => {
        onSuccess?.(data);
      });
    }
  };

  sizes = sizes?.filter((i) => i.quantity > 0);

  return (
    <Form context={form} onSubmit={handleSubmit} className="order-item-form">
      <>
        {fields.includes('size') && <SizeSelectField required label="Taille" error={errors.size} sizes={sizes} />}

        {fields.includes('quantity') && (
          <SizeQuantityField
            error={errors.quantity}
            max={sizes.find((i) => i.slug === form.values.size)?.quantity || 0}
            min={1}
          />
        )}
      </>

      {!Boolean(cart?.customer) && form.values.size && (
        <div className="">
          <CustomerFields.LName error={errors.lastst_name} />
          <CustomerFields.FName error={errors.first_name} />
          <CustomerFields.Phone error={errors.phone} />
          <CustomerFields.Email error={errors.email} />
        </div>
      )}

      {children}
    </Form>
  );
};

export const CustomerForm = ({ onCancel }: { onCancel?: (p?: any) => void }) => {
  const form = useForm<TCustomer>({ first_name: 'john', last_name: 'mickowi', email: '', phone: '32443443343' });
  const sharedProps = {};
  const { errors } = form;

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    postCustomer(form.values)
      .then(({ data }) => {
        console.log(data);
      })
      .catch((err) => {
        form.setErrors(err);
      });
  };

  return (
    <Form context={form} onSubmit={handleSubmit}>
      <CustomerFields.LName error={errors.lastst_name} {...sharedProps} />
      <CustomerFields.FName error={errors.first_name} {...sharedProps} />
      <CustomerFields.Phone error={errors.phone} {...sharedProps} />
      <CustomerFields.Email error={errors.email} {...sharedProps} />

      <div className="d-flex justify-content-between">
        <div>{onCancel && <Button onClick={onCancel}>Annuler</Button>}</div>
        <Form.Submit value={'Envoyer message >>'} />
      </div>
    </Form>
  );
};

export const ProductSearchForm = (props: { className?: string }) => {
  const [search, setSearch] = useSearchParams();

  return (
    <form {...props} className={getCN(['p-search-form', props.className])}>
      <Inputs.SearchInput
        // required
        value={search.get('q') || ''}
        onChange={(e) => {
          setSearch({ q: e.target.value });
        }}
        placeholder="Cherchez un produit ..."
      />
    </form>
  );
};
